.container {
  display: flex;
  flex-direction: column;
  justify-content: center;

  width: 400px;
  height: 500px;
  border-radius: 15px;
  background-color: #2b5770;
  margin-left: auto;
  margin-right: auto;
}
.temp {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  border: 4px solid whitesmoke;
  margin-left: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
h1{
    color:white;
}
button {
  background: #5f5e5d;
  color: white;
  font-size: 32px;
  border: 4px solid whitesmoke;
  padding: 25px 35px;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  cursor: pointer;
}
button:hover{
    background: #dab28a;
}
button:active{
    background-color: #a1eca3;
    box-shadow: 0 2px #666;
    transform: translateY(2px);
}
.btn{
    display: flex;
}
